/* src/components/Contact.css */

.contact {
  text-align: center;
  padding: 50px 20px;
  background-color: whitesmoke;
}

h2 {
  font-size: 35px;
  margin-bottom: 20px;
  border-bottom: 1px solid red;
}

p {
  font-size: 1.2rem;
  line-height: 1.5;
}

.label,
.value {
  display: inline-block;
  width: 100%; /* Adjust the width as needed */
  text-align: center;
}

.label {
  font-size: 17px;
  font-weight: bold;
}

.value {
  font-size: 25px;
  font-weight: bold;
  color: #007bff; /* Adjust the color as needed */
}

.social-icons {
  margin-top: 20px;
  background-color: #007bff;
  width: fit-content;
  margin: 0 auto; /* Center the social icons horizontally */
  padding: 10px; /* Add some padding for better spacing */
  border-radius: 10px; /* Optional: Add rounded corners */
}

.social-icons a {
  display: inline-block;
  margin: 0 10px;
}

.social-icons i {
  font-size: 15px;
}
/* Responsive Styles */
@media (max-width: 768px) {
  p {
    font-size: 20px;
  }
  .label,
  .value {
    width: 100%; /* Make them full width on smaller screens */
    text-align: center;
    margin-bottom: 5px; /* Add some space between label-value pairs */
  }
}

@media (max-width: 576px) {
  h2 {
    font-size: 30px;
  }

  p {
    font-size: 20px;
  }
  .social-icons{
  padding: 10px 5px; 
  }

  .label,
  .value {
    width: 100%; /* Make them full width on smaller screens */
    text-align: center;
    margin-bottom: 5px; /* Add some space between label-value pairs */
  }
  
}
