/* components/Testimonials.css */

.testimonials {
  padding: 50px 20px;
  text-align: center;
}

.testimonials-title {
  font-size: 2rem;
  margin-bottom: 30px;
  border-bottom: 1px solid red;
}

.testimonial-card {
  max-width: 500px;
  width: 70%;
  margin: 10px auto;
  background: #251c1c;
  border-radius: 50px;
}

.slick-center .testimonial-card {
  transform: scale(1);
  transition: transform 1s ease-in-out;
}

.testimonial-text {
  padding: 20px;
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
  color: pink;
}

.testimonial-author {
  color: hotpink;
  font-size: 20px;
  font-style: italic;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .testimonial-card {
    width: 80%;
  }

  .testimonial-text {
    font-size: 20px;
  }

  .testimonial-author {
    font-size: 18px;
  }
}

@media (max-width: 576px) {
  .testimonial-card {
    width: 80%;
  }

  .testimonial-text {
    font-size: 20px;
  }

  .testimonial-author {
    font-size: 18px;
  }
}
